import React, { useState, useEffect } from 'react';
import './listteam.css';
import TeamItem from './TeamItem';
import axios from 'axios';
import { BASE_URL } from '../../../App';
import rougui from '../../assets/images/rougui.jpeg';
import sadio from '../../assets/images/sadio.jpeg';
import mamadou from '../../assets/images/mamadou.jpeg';

const TeamList = ({ nbr }) => {

    const [serteams, setserteams] = useState({ asError: false, data: [] });

    useEffect((() => {
        async function fetchAboutTeams() {
            var data = await axios.get(`${BASE_URL}about/`);
            console.log(data.data);
            setserteams(data.data);
        }
        fetchAboutTeams();
    }), []);

    return (
        <div className='teamList'>
            <div className='badg-l-para'>
                <span className='text-badg-l-para'>our team</span>
            </div>
            <span className='text-l-para'>
                Our dedicated team
            </span>
            <div className='t-list'>
                <TeamItem id={0} img={sadio} fullName="Mr Amadou Sadio" />
                <TeamItem id={1} img={rougui} fullName="Mm Rougui"/>
                <TeamItem id={2} img={mamadou} fullName="Mr Mamadou"/>
                {/* <TeamItem />
                <TeamItem /> */}
            </div>
        </div>
    )
}

export default TeamList